<template>
  <div class="pageContent">
    <el-form :model="ruleForm" ref="ruleForm" label-width="140px" class="demo-ruleForm">
      <!-- 基本信息 -->
      <el-card class="box-card" style="margin-bottom:20px;">
        <div class="pageHead">基本信息</div>
        <el-form-item label="商品标题：" required>
          <el-input style="width:40%;" v-model.trim="ruleForm.name" maxlength="50" show-word-limit placeholder="请输入商品标题"></el-input>
        </el-form-item>
        <el-form-item label="商品描述：" required>
          <el-input style="width:40%;" v-model.trim="ruleForm.abstracts" maxlength="50" show-word-limit placeholder="商品标语用来介绍商品特点，吸引用户购买，若有促销宣传语时将会覆盖商品标语"></el-input>
          <el-button type="text" @click="spms = true">查看示例</el-button>
        </el-form-item>
        <el-form-item label="分享描述：" required>
          <el-input type="textarea" style="width:40%;" v-model="ruleForm.shareDescription" maxlength="100" show-word-limit placeholder="请输入分享描述"></el-input>
          <el-button type="text" @click="fxms = true">查看示例</el-button>
        </el-form-item>
        <el-form-item label="商品编码：" required>
          <el-input style="width:40%;" @blur="blurCode" v-model.trim="ruleForm.code" maxlength="20" show-word-limit placeholder="为了方便商品查找时的准确性，请维护商品编码"></el-input>
        </el-form-item>
        <el-form-item label="商品分类：" required>
          <el-cascader
            v-model="ruleForm.shopCategoryIdsArr"
            :options="category"
            :props="props"
            clearable></el-cascader>
          <el-button @click="getCategory()" style="margin:0 20px">刷新</el-button>
          <el-button @click="newPage1" type="primary">添加分类</el-button>
        </el-form-item>
        <el-form-item label="商品图片：" required>
          <div>
            <el-button :disabled="goodsImg.length == 5" type="primary" @click="showImgMaterialDia">添加图片<i class="el-icon-upload el-icon--right"></i></el-button>
            <span style="color:#909399;">已上传{{goodsImg.length}}张，还可上传{{5-goodsImg.length}}张</span>
            <div style="color:#909399;">建议尺寸800*800像素，你可以拖拽图片调整顺序，最多上传5张，仅支持jpg、png、gif格式</div>
            <div style="color:#F56C6C;">最少需要添加一张商品图片</div>
          </div>
          <div class="flex-row-start" style="padding:20px 0;">
            <div class="li" v-dragging="{ item: li, list: goodsImg, group: 'index' }" v-for="(li,index) in goodsImg" :key="index">
              <el-popover placement="bottom" trigger="hover">
                <el-button @click.stop="deleteImg(index)" type="text" style="color:#909399;" icon="el-icon-circle-close">删除</el-button>
                <el-image @click="showBig(li.imageUrl)" slot="reference" :preview-src-list="bigImg" style="width: 150px; height: 150px" :src="li.imageUrl" fit="fill"></el-image>
              </el-popover>
            </div>
          </div>
        </el-form-item>
        <el-form-item label="商品视频：">
          <div>
            <el-button :disabled="videoForm.length == 1" type="primary" @click="showVideoMaterialDia">添加视频<i class="el-icon-upload el-icon--right"></i></el-button>
            <el-button v-if="videoForm.length == 1" type="primary" @click="videoForm = []">删除视频</el-button>
            <span style="color:#909399;">仅可上传1条视频</span>
          </div>
          <div class="flex-row-start" style="padding:20px 0;">
            <video
              v-if="videoForm.length == 1"
              slot="reference"
              :src="videoForm[0].imageUrl"
              style="width:300px;height:200px;"
              class="avatar video-avatar"
              controls="controls">
              您的浏览器不支持视频播放
            </video>
          </div>
          <div class="infoFont">视频最大支持20M，建议时长9-30秒，建议视频宽高比16:9</div>
        </el-form-item>
        <el-form-item label="视频封面：" v-if="videoForm.length == 1">
          <el-image style="width: 200px; height: 200px" :src="videoForm[0].coversUrl" fit="fill"></el-image>
        </el-form-item>
      </el-card>
      <!-- 规格 & 价格 & 库存 -->
      <el-card class="box-card gg" style="margin-bottom:20px;">
        <div class="pageHead">规格 & 价格 & 库存</div>
         <el-form-item label="商品规格：" required>
           <el-radio-group v-model="ruleForm.skuType">
            <el-radio label="0">统一规格</el-radio>
            <el-radio label="1">多规格</el-radio>
          </el-radio-group>
        </el-form-item>
        <!-- 单规格 -->
        <div v-if="ruleForm.skuType == 0">
          <el-form-item label="成本价：">
            <el-input-number 
              v-model="skuType0.originPrice" 
              placeholder="请输入成本价"
              :precision="2"
              :controls="false"
              :min="0"></el-input-number>
          </el-form-item>
          <el-form-item label="划线价：">
            <el-input-number 
              :controls="false"
              :precision="2"
              placeholder="请输入划线价"
              v-model="skuType0.linePrice" 
              :min="0"></el-input-number>
          </el-form-item>
          <el-form-item label="销售价：" required>
            <el-input-number 
              :controls="false"
              placeholder="请输入销售价"
              :precision="2"
              v-model="skuType0.salePrice" 
              :min="0.01"></el-input-number>
          </el-form-item>
          <el-form-item label="库存：" required>
            <el-input-number 
              :controls="false"
              step-strictly
              placeholder="请输入库存"
              v-model="skuType0.inventory" 
              controls-position="right" 
              :min="0"
              ></el-input-number>
          </el-form-item>
          <el-form-item label="单位：">
            <el-input style="width:180px;" v-model="skuType0.unit" placeholder="请输入单位"></el-input>
          </el-form-item>
          <el-form-item label="规格编码：">
            <el-input style="width:180px;" v-model="skuType0.skuCode" placeholder="请输入规格编码"></el-input>
          </el-form-item>
        </div>
        <!-- 多规格 -->
        <div class="info_box" v-if="ruleForm.skuType == 1">
          <!-- 规格表 -->
          <div class="spec_con">
            <span class="name">商品规格：</span>
            <div class="spec_box">
              <div class="spec_row_box" v-for="(item,index) in specArr" :key="index">
                <div class="spec_name_box">
                  <span class="spec_name">规格名：</span>
                  <el-input style="width:160px;" type="text" placeholder="规格名" v-model="item.specName" maxlength="4" show-word-limit></el-input>
                  <i class="close_btn" @click="deletRow(index)">×</i>
                </div>
                <div class="spec_value_box">
                  <span class="spec_value_name">规格值：</span>
                  <div class="right_box">
                    <div class="input_box" v-for="(sp,idx) in item.specList" :key="idx">
                      <el-input style="width:160px;"  @input="creatData" type="text" placeholder="规格值" v-model="sp.specValue" maxlength="20" show-word-limit></el-input>
                      <i class="close_spec_value" @click.stop="deletSpecValue(index,idx)">×</i>
                    </div>
                    <i class="add_spec_value" @click.stop="addSpecValue(index)">添加规格值</i>
                  </div>
                </div>
              </div>
              <div :class="showAddSpec ? 'add_spec_btn' : 'add_spec_btn default' " @click.stop="addSpecRow">添加规格项</div>
            </div>
          </div>
          <!-- 明细表 -->
          <div class="spec_con">
            <span class="name">规格明细：</span>
            <!-- 一、二级规格 -->
            <div class="spec_box" style="padding:0;border-bottom:0 none;">
              <div class="title_box">
                <span class="one_spec_name">{{specArr[0] && specArr[0].specName}}</span>
                <span class="two_spec_name"  v-if="specArr[1] && specArr[1].specName">{{specArr[1] && specArr[1].specName}}</span>
                <span class="origin_price_name">成本价</span>
                <span class="line_price_name">划线价</span>
                <span class="sale_price_name"><i style="color:red;">* </i> 销售价</span>
                <span class="inventory_name"><i style="color:red;">* </i> 库存</span>
                <span class="unit_name"><i style="color:red;">* </i> 单位</span>
                <span class="specNo_name">规格编码</span>
              </div>
              <div class="rows" v-for="(item,index) in specData" :key="index">
                <p class="td">{{item.specValue}}</p>
                <p class="td border" v-if="specArr[1] && specArr[1].specName">
                  <span class="color" v-for="(nameChild,nameIdx) in item.children" :key="nameIdx" v-show="nameChild.specValue">{{nameChild.specValue}}</span>
                </p>
                <p class="td">
                  <el-input-number style="width:90%;" v-for="(priceChild,priceIdx) in item.children" :key="priceIdx" v-model="priceChild.originPrice" :min="0.01" :precision="2" :controls="false"></el-input-number>
                </p>
                <p class="td">
                  <el-input-number style="width:90%;" v-for="(lineChild,lineIdx) in item.children" :key="lineIdx" v-model="lineChild.linePrice" :min="0.01" :precision="2" :controls="false"></el-input-number>
                </p>
                <p class="td">
                  <el-input-number style="width:90%;" v-for="(saleChild,saleIdx) in item.children" :key="saleIdx" v-model="saleChild.salePrice" :min="0.01" :precision="2" :controls="false"></el-input-number>
                </p>
                <p class="td">
                  <el-input-number style="width:90%;"  v-for="(invenChild,invenIdx) in item.children" :key="invenIdx" v-model="invenChild.inventory" :min="0" :precision="0" :controls="false"></el-input-number>
                </p>
                <p class="td flex2">
                  <el-input style="width:90%;" v-for="(unitChild,unitIdx) in item.children" :key="unitIdx" v-model.trim="unitChild.unit"></el-input>
                </p>
                <p class="td">
                  <el-input style="width:90%;" v-for="(noChild,noIdx) in item.children" :key="noIdx" v-model.trim="noChild.skuCode"></el-input>
                </p>
              </div>
            </div>
          </div>
          <div class="set_box">
            <span class="set_title">批量设置：</span>
            <el-form class="flex-row-start flex-wrap" ref="form" :model="publicObj" label-width="80px">
              <el-form-item label="成本价">
                <el-input-number v-model="publicObj.originPrice" placeholder="请输入成本价" :min="0.01" :precision="2" :controls="false"></el-input-number>
              </el-form-item>
              <el-form-item label="划线价">
                <el-input-number v-model="publicObj.linePrice" placeholder="请输入划线价" :min="0.01" :precision="2" :controls="false"></el-input-number>
              </el-form-item>
              <el-form-item label="销售价">
                <el-input-number v-model="publicObj.salePrice" placeholder="请输入销售价" :min="0.01" :precision="2" :controls="false"></el-input-number>
              </el-form-item>
              <el-form-item label="库存">
                <el-input-number v-model="publicObj.inventory" placeholder="请输入库存数" :min="0" :precision="0" :controls="false"></el-input-number>
              </el-form-item>
              <el-form-item label="单位">
                <el-input v-model.trim="publicObj.unit" placeholder="请输入单位"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="changeParmas">确 定</el-button>
                <el-button @click="cancel">重 置</el-button>
              </el-form-item>
            </el-form>
          </div>
        </div>
      </el-card>
      <!-- 商品参数 -->
      <el-card class="box-card" style="margin-bottom:20px;">
        <div class="pageHead">商品参数</div>
        <el-form-item label="参数：">
          <el-button type="primary" @click="addParameter" :disabled="parameter.length == 8 ">添加参数<i class="el-icon-circle-plus-outline el-icon--right"></i></el-button>
          <span style="margin-left:20px;color:#909399;">最多可添加8条参数</span>
        </el-form-item>
        <el-form-item label="" v-if="parameter.length > 0">
          <el-table :data="parameter" style="width: 800px">
            <el-table-column label="参数名" width="350">
              <template slot-scope="scope">
                <el-input
                  type="text"
                  placeholder="请输入参数名"
                  v-model="scope.row.key"
                  maxlength="10"
                  show-word-limit
                ></el-input>
              </template>
            </el-table-column>
            <el-table-column label="参数值" width="350">
              <template slot-scope="scope">
                <el-input
                  type="text"
                  placeholder="请输入参数值"
                  v-model="scope.row.value"
                  maxlength="15"
                  show-word-limit
                ></el-input>
              </template>
            </el-table-column>
            <el-table-column label="操作" width="100">
              <template slot-scope="scope">
                <el-button type="text" @click="deleteParameter(scope.$index)">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-form-item>
      </el-card>
      <!-- 商品详情 -->
      <el-card class="box-card" style="margin-bottom:20px;">
        <div class="pageHead">商品详情</div>
        <el-form-item label="商品详情：" required>
          <div id="editor" style="width:600px;height:300px;"></div>
        </el-form-item>
      </el-card>
      <!-- 物流信息 -->
      <el-card class="box-card" style="margin-bottom:20px;">
        <div class="pageHead">物流信息</div>
        <el-form-item v-if="selffedchStatus == 0" label="提示：">
          <div>“到店自提”需在店铺设置开启后生效 
            <el-button style="margin-left:20px;" @click="newPage2" type="primary">去开启</el-button>
            <el-button type="text" @click="getStatus">刷新</el-button>
          </div>
        </el-form-item>
        <el-form-item label="商品物流信息：" required>
          <el-checkbox v-model="ruleForm.isCompanyType">物流</el-checkbox>
          <el-checkbox :disabled="selffedchStatus == 0" v-model="ruleForm.isSelffetchType">到店自提</el-checkbox>
        </el-form-item>
        <el-form-item v-if="ruleForm.isCompanyType" label="商品物流信息：" required>
          <el-radio-group v-model="ruleForm.isFreeShipping">
            <el-radio label="1">包邮</el-radio>
            <el-radio label="0">不包邮</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="运费模板：" v-if="ruleForm.isCompanyType && ruleForm.isFreeShipping == 0" required>
          <el-select v-model="ruleForm.freightTemplateId" placeholder="请选择模板">
            <el-option v-for="(item,index) in logisticsTemplate" :key="index" :label="item.templateName" :value="item.id"></el-option>
          </el-select>
          <el-button @click="getLogisticsTemplate()" style="margin:0 20px">刷新</el-button>
          <el-button @click="newPage" type="primary">新建</el-button>
        </el-form-item>
      </el-card>
      <!-- 其他信息 -->
      <el-card class="box-card" style="margin-bottom:20px;">
        <div class="pageHead">其他信息</div>
        <el-form-item label="上架时间：">
          <el-radio-group v-model="ruleForm.salesStatus">
            <el-radio label="0">立刻上架</el-radio>
            <el-radio label="2">放入待售</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="销量显示：">
          <el-radio-group v-model="ruleForm.showSales">
            <el-radio label="1">开</el-radio>
            <el-radio label="0">关</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="评价显示：">
          <el-radio-group v-model="ruleForm.evaluate">
            <el-radio label="1">开</el-radio>
            <el-radio label="0">关</el-radio>
          </el-radio-group>
        </el-form-item>
      </el-card>
      <!-- 发布按钮 -->
      <el-form-item class="flex-row-center">
        <el-button @click="$router.push({path:'/commodity/commodityList'})">返 回</el-button>
        <el-button :loading="subLoading" type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
      </el-form-item>
    </el-form>
    <!-- 图片库 -->
    <el-dialog title="图片库" :visible.sync="showImgMaterial" width="70%" :append-to-body="true">
      <imageMaterial
        :limitType="limitType"
        :getPost="getPost"
        :multiple="true"
        :totalNum="5"
        :selectNum="selectNum"
        @onlyclose="onlyclose"
        @subMit="subMit"
      ></imageMaterial>
    </el-dialog>
    <!-- 视频库 -->
    <el-dialog title="视频库" :visible.sync="showVideoMaterial" width="70%">
      <videoMaterial
        :limitType="true"
        :getPost="getPost"
        :multiple="false"
        :totalNum="1"
        :selectNum="0"
        @onlyclose="onlyCloseVideo"
        @subMit="subMitVideo"
      ></videoMaterial>
    </el-dialog>
    <!-- 查看示例 -->
    <el-dialog title="商品描述示例" :visible.sync="spms" width="30%" center>
      <img style="max-width:100%;margin:0 auto;" src="../../../assets/images/commodity/goodShare2.png" alt="">
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="spms = false">确 定</el-button>
      </span>
    </el-dialog>
    <el-dialog title="分享描述示例" :visible.sync="fxms" width="30%" center>
      <div class="tac">当商品参加营销活动时，展示活动分享标题及文案。</div>
      <img style="max-width:100%;margin:0 auto;" src="../../../assets/images/commodity/goodShare1.png" alt="">
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="fxms = false">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import imageMaterial from '@/components/shopMaterial/imageMaterial';
import videoMaterial from '@/components/shopMaterial/videoMaterial';
import { getFreightTemplateInfoListNotPage } from '@/api/logistics/logisticsTemplate'
import { getProductCategoryShopAllList } from '@/api/commodity/commodityClassify'
import { insertProduct , isExistProductCode } from '@/api/commodity/addCommodity'
import { getShopSelffetch } from '@/api/logistics/selfPickUp'
export default {
  data(){
    return{
      spms:false,
      fxms:false,
      subLoading:false,
      ue1: "ue1",               // 富文本
      ruleForm:{
        salesStatus:'0',
        evaluate:'1',
        isFreeShipping:'1',
        showSales:'1',
        freightTemplateId:'',
        isCompanyType:false,
        isSelffetchType:false,
        skuType:'0'
      },
      skuType0:{
        unit:'件',                      // 单位
      },
      showImgMaterial:false,          // 图片库弹出框
      showVideoMaterial:false,
      limitType:false,                
      getPost:false,
      selectNum:0,
      goodsImg:[],
      imgMaterialType:[],             // 图片库归属
      bigImg:[],
      logisticsTemplate:[],           // 运费模板
      category:[],                    // 全部分类
      parameter:[],

      attrs:[],
      btnStatus:false,           //是否能继续增加
      originPrice:'',
      linePrice:'',
      salePrice:'',  
      inventory:'',
      unit:'',
      props: { multiple: true },
      checkList:[],
      weight:'',
      specArr: [],                    //规格名与规格值
      specData: [],                   //规格明细数据
      showAddSpec: true,              //是否禁用添加规格按钮
      publicObj:{                     //批量的修改对象
      },
      selffedchStatus:'',
      codeType:false,
      videoForm:[]
    }
  },
  created(){
    this.getLogisticsTemplate();
    this.getCategory()
    this.getStatus()
  },
  methods:{
    blurCode(){
      isExistProductCode(this.shop.shopId,this.ruleForm.code).then(res=>{
        console.log('验证code',res)
        if(res.code != 200){
          this.$message.error(res.message);
        }
        this.codeType = res.code == 200
      })
    },
    // 获取是否开启了自提状态
    getStatus(){
      getShopSelffetch(this.shop.shopId).then(res=>{
        console.log('是否开启了自提',res)
        this.selffedchStatus = res.data
      })
    },
    // 添加规格项行
    addSpecRow() {
      // console.log('新增规格行');
      let obj = {
        specName: '',
        specList: [
          {specValue:''}
        ]
      }
      this.specArr.push(obj);
      if(this.specArr.length >= 2){
        this.showAddSpec = false;
        return;
      }
      
    },
    // 删除当前规格行
    deletRow(index) {
      this.specArr.splice(index, 1);
      if (this.specArr.length < 2) {
        this.showAddSpec = true;
      }
      // 重新生成表格数据
      this.creatData();
    },
    // 添加规格值
    addSpecValue(index) {
      let obj = {
        specValue: ''
      }
      this.specArr[index].specList.push(obj);
    },
    // 删除规格值
    deletSpecValue(index, idx) {
      this.specArr[index].specList.splice(idx, 1);
      // 重新生成表格数据
      this.creatData();
    },
    // 实时生成数据
    creatData(){
      console.log(this.specArr);
      this.specData = [];
      this.specArr[0].specList.forEach((item,i)=>{
        let specObj = {
          specKey:this.specArr[0].specName,
          specValue:item.specValue,
          children:[]
        }
        this.specData.push(specObj);
        if(this.specArr[1]){
          this.specArr[1].specList.forEach(sku=>{
            let obj = {
              specKey:this.specArr[1].specName,
              specValue:sku.specValue,
              originPrice:undefined,
              linePrice:undefined,
              salePrice:undefined,
              inventory:undefined,
              unit:'件',
              skuCode:'',
              isOpen:true
            };
            specObj.children.push(obj);
          });
        }else{
          let obj = {
            originPrice:undefined,
            linePrice:undefined,
            salePrice:undefined,
            inventory:undefined,
            unit:'件',
            skuCode:'',
            isOpen:true,
          };
          specObj.children.push(obj);
        }
      })
      console.log(this.specData);
    },
    // 检查数组内是否有重复对象
    removeRepeat(arr){
      var find = false;
      for(var i = 0; i < arr.length; i++){
        for (var j = i + 1; j < arr.length; j++){
          if(arr[i].specName == arr[j].specName){ 
            find = true; break;
          }
        }
        if (find) break;
      }
      return find;
    },
    // 批量修改参数
    changeParmas(){
      if(!this.publicObj.originPrice && !this.publicObj.linePrice && !this.publicObj.salePrice && !this.publicObj.inventory && !this.publicObj.unit){
        this.$message.error('请输入批量设置参数');
        return;
      }
      this.specData.forEach(item=>{
        item.children.forEach(item1=>{
          if(this.publicObj.originPrice) item1.originPrice = this.publicObj.originPrice;
          if(this.publicObj.linePrice) item1.linePrice = this.publicObj.linePrice;
          if(this.publicObj.salePrice) item1.salePrice = this.publicObj.salePrice;
          if(this.publicObj.inventory) item1.inventory = this.publicObj.inventory;
          if(this.publicObj.unit) item1.unit = this.publicObj.unit;
        });
      });
    },
    // 取消
    cancel(){
      for( let i in this.publicObj){
        this.publicObj[i] = undefined;
      }
    },
    submitForm(formName) {
      console.log(this.ruleForm)
      // 商品标题
      if(!this.ruleForm.name){
        this.$message.error('请填写商品标题');
        return;
      }
      // 商品描述
      if(!this.ruleForm.abstracts){
        this.$message.error('请填写商品描述');
        return;
      }
      // 分享描述
      if(!this.ruleForm.shareDescription){
        this.$message.error('请填写分享描述');
        return;
      }
      // 商品编码
      if(!this.ruleForm.code){
        this.$message.error('请填写商品编码');
        return;
      }
      if(!this.codeType){
        this.$message.error('商品编码重复，请重新输入');
        return;
      }
      // 分类
      if(!this.ruleForm.shopCategoryIdsArr || this.ruleForm.shopCategoryIdsArr.length == 0){
        this.$message.error('请选择商品分类');
        return;
      }else{
        let arr = [];
        this.ruleForm.shopCategoryIdsArr.forEach(e=>{
          let obj = {
            pid:e[0],
            id:e[1]
          }
          arr.push(obj)
        }) 
        this.ruleForm.shopCategoryIds = arr;
      }
      // 商品图片
      if(this.goodsImg.length == 0){
        this.$message.error('请上传商品图片');
        return;
      }
      this.ruleForm.imageUrl = this.goodsImg[0].imageUrl
      this.ruleForm.goodsImg = this.goodsImg
      // 商品规格
      if(this.ruleForm.skuType == 0){
        if(!this.skuType0.salePrice){
          this.$message.error('请填写销售价');
          return;
        }
        if(this.skuType0.linePrice && this.skuType0.linePrice <= this.skuType0.salePrice){
          this.$message.error('划线价应大于销售价');
          return;
        }
        if(this.skuType0.inventory == undefined){
          this.$message.error('请填写库存');
          return;
        }
        let arr = []
        arr.push(this.skuType0)
        this.ruleForm.productSkuList = arr
      }
      if(this.ruleForm.skuType == 1){
        console.log('specArr',this.specArr)
        console.log('specData',this.specData)
        if(this.specData.length == 0){
          this.$message.error('请添加商品规格');
          return;
        }
        // 验证规格值规格名
        let errSpecName = this.specArr.filter( e => !e.specName)
        if(errSpecName.length > 0){
          this.$message.error('请完善商品规格列表中的规格名');
          return;
        }
        let specvaluearr = []
        this.specArr.forEach( e=> {
          e.specList.forEach(item => {
            specvaluearr.push(item)
          })
        })
        let errSpecValue = specvaluearr.filter( e => !e.specValue)
        if(errSpecValue.length > 0){
          this.$message.error('请完善商品规格列表中的规格值');
          return;
        }
        // 验证列表中的数据
        let sepcArrNew = []
        this.specData.forEach(e=>{
          e.children.forEach(item => {
            sepcArrNew.push(item)
          })
        })
        let specErrArr = sepcArrNew.filter( item=> item.inventory == undefined || !item.salePrice || !item.unit)
        if(specErrArr.length > 0){
          this.$message.error('请完善商品规格中的必填值');
          return;
        }
        let specErrArr1 = sepcArrNew.filter( item=> item.linePrice && item.linePrice <= item.salePrice)
        if(specErrArr1.length > 0){
          this.$message.error('规格中的划线价应大于销售价');
          return;
        }
        this.ruleForm.productSkuList = this.specData
        this.ruleForm.specArr = this.specArr
      }
      // 参数
      if(this.parameter.length != 0){
        let errArr = this.parameter.filter( e=> !e.key || !e.value)
        console.log('errArr',errArr)
        if(errArr.length > 0){
          this.$message.error('请完善填写参数');
          return;
        }else{
          this.ruleForm.parameter = this.parameter;
        }
      }
      // 富文本
      let content = this.ue1.getContent();
      if(!content){
        this.$message.error('请完善商品详情');
        return;
      }else{
        this.ruleForm.content = content;
      }
      // 物流信息
      if(!this.ruleForm.isCompanyType && !this.ruleForm.isSelffetchType){
        this.$message.error('请选择物流信息');
        return;
      }
      this.ruleForm.isCompany = this.ruleForm.isCompanyType ? '1' : '0'
      this.ruleForm.isSelffetch = this.ruleForm.isSelffetchType ? '1' : '0'
      if(this.ruleForm.isFreeShipping == 0 && !this.ruleForm.freightTemplateId){
        this.$message.error('请选择运费模板');
        return;
      }
      this.ruleForm.shopId = this.shop.shopId
      this.ruleForm.videoForm = this.videoForm || []
      console.log('牛逼成功率 ！！',JSON.stringify(this.ruleForm))
      this.subLoading = true;
      let params = JSON.stringify(this.ruleForm)
      insertProduct(params).then(res=>{
        console.log('发布商品',res)
        if(res.code == 200){
          this.$message({
            message: '发布成功',
            type: 'success'
          });
          setTimeout(()=>{
            this.$router.replace({
              path:'/commodity/commodityList'
            })
          },1000)
        }else{
          this.subLoading = false;
          this.$message.error(res.message);
        }
      })
      
    },
    // 点击添加参数
    addParameter(){
      this.parameter.push({key:'',val:''})
    },
    // 删除参数
    deleteParameter(i){
      console.log(i)
      this.parameter.splice(i, 1)
    },
    // 打开运费模板新页面
    newPage(){
      let details = this.$router.resolve({
        name: "新建运费模板",
      });
      window.open(details.href, "_blank")
    },
