    },
    // 获取运费模板
    getLogisticsTemplate(){
      getFreightTemplateInfoListNotPage(this.shop.shopId).then(res=>{
        console.log('获取运费模板',res)
        this.logisticsTemplate = res.data.list
        let defaultA = res.data.list.find(item => {
          return item.isDefault == 0
        })
        if(defaultA) this.ruleForm.freightTemplateId = defaultA.id
        console.log('defaultA',defaultA)
      })
    },
    // 获取全部分类
    getCategory(){
      getProductCategoryShopAllList(this.shop.shopId).then(res=>{
        console.log('获取店铺分类',res)
        this.category = res.data
      })
    },
    showBig(e){
