    subMit(e){
      console.log('点击了确定',e)
      this.showImgMaterial = false;
      this.getPost = false;
      // this.goodsImg.concat(e);
      if(this.imgMaterialType == 'goodsImg') e.forEach(item => this.goodsImg.push(item))
      if(this.imgMaterialType == 'richText'){
        let str = ''
        e.forEach(item => {
          str += `<p><img style="max-width:100%;" src="${item.imageUrl}" alt=""></p>`
        })
        this.ue1.execCommand('inserthtml', str);
        console.log('上传给富文本的图片',str)
      }
