    });
    let _this = this;
    this.$nextTick(() => {
      // 注册上传图片按钮
      UE.registerUI('diyimg', function(editor, uiName) {
        var btn = new UE.ui.Button({
          name: uiName, //按钮的名字
          title: "添加图片", //提示
          cssRules: 'background-position: -380px 0;',
          onclick: () => {
            // 派发事件
            _this.imgMaterialType = 'richText';
            _this.selectNum = 0;
            _this.getPost = true;
            _this.showImgMaterial = true;
            _this.limitType = false;
          }
        });
        return btn;
      }, 2);
      this.ue1 = UE.getEditor('editor', {
