  destroyed() {
    this.ue1.destroy();
    console.log("销毁");
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ]),
    tableData(){
      var attrs = this.attrs;
      var len = attrs.length;
      if(len==0){
        return;
      }
      var tData = [];
      //初始化tableData
      for (var i = 0; i < len; i++) {
        var row = {};
        row['pName'] = attrs[i]['pName'];
        row['spec'] = [];
        row['originPrice'] = '';
        row['salePrice'] = '';
        row['inventory'] = '';
        row['unit'] = '';
        row['skuCode'] = '';
        var len2 = attrs[i]['spec'].length;
        var specLen = 0;
        for (var j = 0; j < len2; j++) {
            var spe = {};
            var cName = attrs[i]['spec'][j]['cName'];
            if(!cName){
              continue;
            }
            ++specLen;
            spe['cName'] = cName;
            row['spec'].push(spe);
        }
        row['specLen'] = specLen;
        tData.push(row);
      }
      //获取rowspan
      for (var k = 0,len3=tData.length; k < len3; k++) {
          var rowspan = 1;
          for (var k1 = k+1; k1 < len3; k1++) {
              var kSpecLen = tData[k1]['specLen']||1;
              rowspan*=kSpecLen;
          }
          tData[k]['rowspan'] = rowspan;
      }
      return tData;
    },
    // 行的规格值
    rows(){
      if(!this.tableData){
        return;
      }
      var rows = 1;
      var tableData = this.tableData;
      var len = tableData.length;
      for (var i = 0; i < len; i++) {
         var specLen = tableData[i]['specLen']||1;
         rows*=specLen;
      }
      //每条rowspan都为1情况
      if(rows==1){
        return tableData[0]['spec'].length;
      }
      return rows;
    },
    // 生成规格表格
    tableList(){
      if(!this.tableData){
        return;
      }
      var rows = this.rows;
      var tList = [];
      var srcData = this.tableData;
      //console.log(srcData);
      for (var i = 0; i < rows; i++) {
        var listItem = {};
        // listItem['specKey'] = srcData[j]['pName']
        // listItem['specValue']= spec[index]['cName']
        //构建动态项
        for (var j = 0; j < srcData.length; j++) {
            //构造第一类目
            var key = srcData[j]['pName'];
            var rowspan = srcData[j]['rowspan'];
            var len = srcData[j]['specLen'];
            if(!len){
              continue;
            }
            var spec = srcData[j]['spec'];
            var index = parseInt(i/rowspan)%len;
            console.log('cName',spec[index]['cName'])
            console.log('key',listItem[key])
            listItem[key] = spec[index]['cName'];
        }
        //构建固定项(price,number)
        listItem['originPrice'] = undefined;
        listItem['linePrice'] = undefined;
        listItem['salePrice'] = undefined;
        listItem['inventory'] = undefined;
        listItem['unit'] = "件";
        listItem['skuCode'] = undefined;
        tList.push(listItem);
      }
      return tList;
    }
  },
  components: {
    imageMaterial,
    videoMaterial
  },
}
